/* Position and sizing of burger button */
.bm-burger-button {
  position: absolute;
  width: 26px;
  height: 20px;
  left: 26px;
  top: 26px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: white;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #ffffff;
  /* padding: 2.5em 1.5em 0; */
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  /* padding: 0.8em; */
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
.menu_logo_div {
  background-color: #1a1a1a;
}
.menu_logo {
  padding-top: 20px;
  padding-left: 15px;
}
.menu_hr {
  border: none;
  height: 1px;
  background-color: rgba(53, 53, 53, 0.308);
}
.menu-item {
  color: black;
  text-decoration: none;
  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.menu_language_div {
  position: absolute;
  right: 0;
  padding-right: 10px;
  /* border: 1px solid red; */
}
.menu_arrow_down {
  padding-left: 10px;
  padding-bottom: 4px;
}
.menu_social {
  width: 100%;
  /* border: 1px solid red; */
  position: absolute;
  top: 92%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  /* align-items: center;/ */
  /* justify-content: center; */
  text-align: center;
}
.menu_social p {
  font-size: 15px;
  color: black;
}
.menu_social div {
  text-align: center;
  border-radius: 10%;
  padding-left: 20px;
  /* margin-left: 15px; */
  padding-top: 10px;
  padding-bottom: 10px;
  display: inline-block;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
